<template>
	<view class="content">
		<view class="item" v-for="item in list" :key="item._id" @click="handleDetail(item._id)">
			<view class="item-title">
				<view class="item-name">
					问答题
				</view>
			</view>
			<view class="item-main">
				{{ item.title }}
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: []
			}
		},
		onLoad(option) {
			this.typeSearch(option)
		},
		methods: {
			handleDetail(id) {
				uniCloud.callFunction({
					name: 'question-detail',
					data: {
						id: id
					}
				})
				.then(res => {
					console.log(res.result);
				});
			},
			typeSearch(option) {
				uniCloud.callFunction({
					name: 'question-list',
					data: {
						type_id: option.type_id
					}
				})
				.then(res => {
					this.list = res.result
					uni.setNavigationBarTitle({
						title: option.name
					});
				});
			}
		}
	}
</script>

<style lang="less" scoped>
	.content {
		display: flex;
		flex-direction: column;
		.item {
			.item-title {
				padding: 10rpx 20rpx;
				background: rgb(26, 115, 232);
				color: #fff;
				font-size: 16rpx;
				overflow:hidden;
				.item-name {
					float: right;
				}
			}
			.item-main {
				padding: 30rpx 50rpx;
			}
			
		}
	}
</style>